'use client';

import { useState, useTransition } from 'react';
import { Bot, Loader2 } from 'lucide-react';

import { Button } from '@/components/ui/button';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
} from '@/components/ui/dialog';
import { forecastNextTrip, ForecastNextTripOutput } from '@/ai/flows/forecast-next-trip';
import { Vehicle } from '@/lib/types';
import { trips } from '@/lib/data';
import { Alert, AlertDescription, AlertTitle } from './ui/alert';

interface ForecastButtonProps {
  vehicle: Vehicle;
}

export function ForecastButton({ vehicle }: ForecastButtonProps) {
  const [isOpen, setIsOpen] = useState(false);
  const [isPending, startTransition] = useTransition();
  const [result, setResult] = useState<ForecastNextTripOutput | null>(null);
  const [error, setError] = useState<string | null>(null);

  const handleForecast = () => {
    setIsOpen(true);
    setResult(null);
    setError(null);
    startTransition(async () => {
      try {
        const currentTrip = trips.find(t => t.vehicleId === vehicle.id && (t.status === 'ON_ROUTE' || t.status === 'POB'));
        if (!currentTrip) {
            setError("Could not find a current active trip for this vehicle to forecast from.");
            return;
        }
        const res = await forecastNextTrip(vehicle, currentTrip);
        setResult(res);
      } catch (e: any) {
        setError(e.message || 'An unexpected error occurred.');
      }
    });
  };

  return (
    <>
      <Button variant="outline" size="sm" onClick={handleForecast}>
        <Bot className="mr-2 h-4 w-4" />
        Forecast
      </Button>
      <Dialog open={isOpen} onOpenChange={setIsOpen}>
        <DialogContent className="sm:max-w-[425px]">
          <DialogHeader>
            <DialogTitle>AI Trip Forecast</DialogTitle>
            <DialogDescription>
              Forecasting the next optimal assignment for{' '}
              <span className="font-bold text-primary">{vehicle.plateNumber}</span>.
            </DialogDescription>
          </DialogHeader>
          <div className="py-4">
            {isPending && (
              <div className="flex items-center justify-center space-x-2">
                <Loader2 className="h-6 w-6 animate-spin" />
                <span>Analyzing pending jobs...</span>
              </div>
            )}
            {error && (
                <Alert variant="destructive">
                    <AlertTitle>Error</AlertTitle>
                    <AlertDescription>{error}</AlertDescription>
                </Alert>
            )}
            {result && (
              <div className="space-y-4">
                {result.nextJobId ? (
                    <>
                        <p className="text-sm text-muted-foreground">{result.reasoning}</p>
                        <div className="rounded-lg border bg-card text-card-foreground shadow-sm p-4 space-y-2">
                            <h4 className="font-semibold">Suggested Next Job</h4>
                            <p>
                                <span className="font-medium">Job ID:</span> {result.nextJobId}
                            </p>
                            <p>
                                <span className="font-medium">Dead Kilometers:</span> ~{result.distanceToNextPickup?.toFixed(1)} km
                            </p>
                        </div>
                         <Button className="w-full" onClick={() => setIsOpen(false)}>Assign Job</Button>
                    </>
                ) : (
                    <p>{result.reasoning}</p>
                )}
              </div>
            )}
          </div>
        </DialogContent>
      </Dialog>
    </>
  );
}
